<template>
  <div class="slider">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="iframeBox">
          <iframe src="#/sliderTabExample"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">sliderTab 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import sliderTab from "cy_static/sliderTab";
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
  &lt;style type="text/css"&gt;
    #myTab{width:375px;position:relative;overflow:hidden;background-color:#F9F9F9}
    #myTab .content{display:table;white-space:nowrap;text-align:center}
    #myTab .item{width:81px;display:inline-block;line-height:50px;color:#2E2D2B;}
    #myTab .item.curr{color:red;font-weight:bold}
  &lt;/style&gt;
  &lt;div id="myTab"&gt;
		&lt;div class="content"&gt;
			&lt;div class="item"&gt;热门&lt;/div&gt;
            &lt;div class="item"&gt;美食餐饮&lt;/div&gt;
            &lt;div class="item"&gt;休闲娱乐&lt;/div&gt;
            &lt;div class="item"&gt;品质出行&lt;/div&gt;
            &lt;div class="item"&gt;电商消费&lt;/div&gt;
            &lt;div class="item"&gt;生活服务&lt;/div&gt;
            &lt;div class="item"&gt;体育&lt;/div&gt;
            &lt;div class="item"&gt;自白&lt;/div&gt;
            &lt;div class="item"&gt;政务&lt;/div&gt;
            &lt;div class="item"&gt;军事&lt;/div&gt;
		&lt;/div&gt;
	&lt;/div&gt;
  &lt;script type="text/javascript"&gt;
      import sliderTab from "cy_static/sliderTab"; 
      sliderTab({
          elem: "#myTab", //窗口元素--要设置高度   
          currIndex: 0,   // 当前tab下标
          content: ".content"// 滚动元素
      });
  &lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>sliderTab</a>(params : Object) : <a>Object</a></p>
						<p>tab标签菜单，支持滑动切换、点击回调，动态改变当前选中值并居中显示；</p>				
            <table class="table-props">
              <tr>
                <th colspan="5">params参数详解</th>
              </tr>
              <tr>
                <th>参数名</th>
                <th>类型</th>
                <th>描述</th>
                <th>默认值</th>
                <th class="text-c">必需</th>
              </tr>
              <tr>
                <td>elem</td>
                <td class="atn">String||Object</td>
                <td>窗口元素</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>content</td>
                <td class="atn">String||Object</td>
                <td>窗口子元素</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>time</td>
                <td class="atn">Number</td>
                <td>滑动时间(毫秒)</td>
                <td>1000</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>drag</td>
                <td class="atn">Number</td>
                <td>阻尔糸数</td>
                <td>0.1</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>moveInt</td>
                <td class="atn">Boolean</td>
                <td>移动整格</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>isSpring</td>
                <td class="atn">Boolean</td>
                <td>启动弹性吸附效果</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr> 
              <tr>
                <td>resetWidth</td>
                <td class="atn">Number</td>
                <td>重置复位宽度(米)</td>
                <td>10</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>currCss</td>
                <td class="atn">String</td>
                <td>默认选中样式</td>
                <td>"curr"</td>
                <td class="text-c">N</td>
              </tr> 
              <tr>
                <td>currIndex</td>
                <td class="atn">Number</td>
                <td>默认选中的窗口</td>
                <td>0</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>isOverMove</td>
                <td class="atn">Boolean</td>
                <td>超过窗口是否能滑动</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr> 
              <tr>
                <td>toInnerIndex</td>
                <td class="atn">Number</td>
                <td>选中TAB向内显示</td>
                <td>1</td>
                <td class="text-c">N</td>
              </tr>  									
              <tr>
                <td>onStart</td>
                <td class="atn">Function</td>
                <td>拖动开始</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>  
              <tr>
                <td>onScroll</td>
                <td class="atn">Function</td>
                <td>滚动事件</td>
                <td>function (left) {}</td>
                <td class="text-c">N</td>
              </tr>  
              <tr>
                <td>onMove</td>
                <td class="atn">Function</td>
                <td>拖动中</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>  
              <tr>
                <td>onStop</td>
                <td class="atn">Function</td>
                <td>拖动结束</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>  
              <tr>
                <td>onClick</td>
                <td class="atn">Function</td>
                <td>tab点击事件</td>
                <td>function (index) {}</td>
                <td class="text-c">N</td>
              </tr>
            </table>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>        
				<ul class="ul-props">
					<li>
						<p><a>pause</a>() : <a>Void</a></p>
						<p>禁止滑动；</p>
					</li>
					<li>
						<p><a>play</a>() : <a>Void</a></p>
						<p>启动滑动；</p>
					</li>
					<li>
						<p><a>reset</a>() : <a>Void</a></p>
						<p>重置操作/让已选中的Tab显示在可视窗口内；</p>
					</li>
					<li>
						<p><a>setCss</a>(index : Number) : <a>Void</a></p>
						<p>单纯的把index设置为选中样式；多用于窗口滑动时临时选中用；</p>
					</li>	
					<li>
						<p><a>setActivated</a>(isActivated) : <a>Void</a></p>
						<p>是否激活；</p>
					</li>	
					<li>
						<p><a>destroy</a>() : <a>Void</a></p>
						<p>销毁实例里所有事件；</p>
					</li>
					<li>
						<p><a>setCurrIndex</a>(index : Number) : <a>Void</a></p>
						<p>设置要选中Tab下标（从0开始）；并在可视窗口中显示</p>
					</li>
				</ul>        
      </div>




    </div> 
  </div>
</template>

<script>
  import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'slider',
  data () {
    return {
      msg: 'slider',
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });
  }
}
</script>  
<style scoped>
  .cy_example .iframeBox{position:absolute;top:43px;width:100%;bottom:0}
</style>



